import BaseAPI from './api';
import { APIPath } from './const';
/* 
userid int64
accesstoken string
reqdate int64
appversion string 
deviceid string
*/

const authorization = {};

const TOPAZ_APPID = 362;

const setAuthorization = (_authorization) => {
  console.log('setAuthorization', _authorization);
  const { muid, maccess_token, appVersion } = _authorization;
  authorization.muid = muid;
  authorization.maccesstoken = maccess_token;
  authorization.appversion = appVersion;
  console.log('setAuthorization', authorization);
};
const getAuthorization = async () => {
  const { appVersion } = window.ZaloPay.appInfo;
  const { data } = await getUserInfo(TOPAZ_APPID);
  setAuthorization({
    appVersion,
    ...data
  });
};
//eslint-disable-next-line
const getUserInfo = (appId) => {
  return new Promise((resolve) => {
    window.ZaloPay.getUserInfo(appId, (resp) => {
      resolve(resp);
    });
    // resolve({
    //   data: {
    //     maccess_token:
    //       'OtsF219x3j6QdMALSQkf2a2fkEuTN_2kDUg6y8k3G7mWJ66ngWOM1Wv34MGs_dE7',
    //     muid: 'JM6hpCchhat7-2Rq7TaLYJqg-ZNCUuK0qfHCsBvF9C0',
    //     appVersion: '3.17'
    //   }
    // });
  });
};

const post = (url, params) =>
  BaseAPI.post(url, { params: { ...authorization, ...params } });
const get = (url, params) =>
  BaseAPI.get(url, { params: { ...authorization, ...params } });

const createPig = () => {
  return post(APIPath.CREATE_PIG);
};

const getPigInfo = () => {
  return get(APIPath.GET_PIG);
};

const smashPig = () => {
  return get(APIPath.SMASH_PIG);
};

const getVouchers = () => {
  return get(APIPath.GET_VOCHERS);
};
const getAppInfo = () => {
  return get(APIPath.GET_APP_INFO);
};

export default {
  getPigInfo,
  smashPig,
  getVouchers,
  getAppInfo,
  createPig,
  getUserInfo,
  getAuthorization,
  setAuthorization
};
