import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
// import { Loading } from '../../components';
import AppActions from '../../reducers/app-reducer';

class Full extends Component {
  async componentDidMount() {
    window.ZaloPay.setProperty(
      {
        navigation: {
          hidden: true
        }
      },
      () => {}
    );
    const { actions } = this.props;
    actions.getAppInfo();
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.isReady) {
      const {history} = this.props;
      history.replace('./introduce');
    }
    
  }

  render() {
    console.log('render', this.props.tasks);
    return (
      <div>
        <Helmet
          htmlAttributes={{ class: 'white-body' }}
          bodyAttributes={{ class: 'white-body' }}
        >
        </Helmet>
        
      </div>
    );
    // return <Loading />;
  }
}

// Full.propTypes = {
//   isReady: PropTypes.bool,
//   toastMsg: PropTypes.string,
//   toastDuration: PropTypes.number,
//   startUp: PropTypes.func,
//   showToast: PropTypes.func
// };
// Full.defaultProps = {
//   toastMsg: '',
//   startUp: () => {},
//   showToast: () => {}
// };

const mapStateToProps = (state) => ({
  isReady: !!state.app.tasks,
  tasks: state.app.tasks,
  toastMsg: state.app.toastMsg,
  toastDuration: state.app.toastDuration
});

const mapDispatchToProps = (dispatch) => ({
  dispatch,
  actions: {
    getAppInfo: () => dispatch(AppActions.getAppInfo)
  }
  
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Full);
