import React from 'react';
import { Route, Switch } from 'react-router-dom';
import IntroducePage from '../containers/page-introduce';

import HomePage from '../containers/page-home';
import MasterPage from '../containers/full';
import GuidePage from '../containers/page-guide';

class App extends React.Component {
  componentDidMount() {
    document.documentElement.addEventListener(
      'touchstart',
      function(event) {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      },
      false
    );
    var lastTouchEnd = 0;
    document.documentElement.addEventListener(
      'touchend',
      function(event) {
        var now = new Date().getTime();
        if (now - lastTouchEnd <= 300) {
          event.preventDefault();
        }
        lastTouchEnd = now;
      },
      false
    );
    window.ZaloPay.setProperty(
      {
        navigation: {
          backgroundColor: '#c7c7cc',
          titleColor: '#000000',
          pullToRefresh: false
        }
      },
      () => {}
    );
  }

  render() {
    return (
      <Switch>
        <Route exact path="/" component={MasterPage} />
        <Route exact path="/home" component={HomePage} />
        <Route exact path="/introduce" component={IntroducePage} />
        <Route exact path="/guide" component={GuidePage} />
      </Switch>
    );
  }
}
export default App;
