import { createReducer, createActions } from 'reduxsauce';
import { setAuthorizationHeader } from '../apis/api';
import TopazAPI from '../apis';
// import { IOS_STORE, ANDROID_STORE, APP_LINK, WEB_LINK } from '../utils/constants';
// import { webos } from '../utils/commons';

/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
  updateUserInfo: ['userInfo'],
  showToast: ['toastMsg', 'toastDuration'],
  updateAppInfo: ['tasks', 'items', 'levels']
});

export const AppTypes = Types;
export default Creators;

/* -------------- Initial State ----------- */

export const INITIAL_STATE = {
  userInfo: null,
  toastMsg: '',
  toastDuration: 0,
  fullScreenImageUrl: '',
  tasks: undefined,
  items: undefined,
  levels: undefined
};

/* ------------- Reducers -------------- */

const updateUserInfo = (state, { userInfo }) => ({ ...state, userInfo });
const showToast = (state, { toastMsg, toastDuration }) => ({
  ...state,
  toastMsg,
  toastDuration: !toastMsg ? 0 : toastDuration || 3000
});

const updateAppInfo = (state, { tasks, items, levels }) => {
  return {
    ...state,
    tasks,
    items,
    levels
  };
};
/* ----------- Hookup Reducers To Types ----------- */

export const reducer = createReducer(INITIAL_STATE, {
  [Types.UPDATE_USER_INFO]: updateUserInfo,
  [Types.SHOW_TOAST]: showToast,
  [Types.UPDATE_APP_INFO]: updateAppInfo
});

/* ----------- Actions for redux-thunk ----------- */

Creators.getAppInfo = async (dispatch) => {
  console.log('Creators.getAppInfo');
  try {
    await TopazAPI.getAuthorization();
    const appInfo = await TopazAPI.getAppInfo();
    console.log('appInfo', appInfo);
    const { tasks, items, levels } = appInfo;
    dispatch(Creators.updateAppInfo(tasks, items, levels));
  } catch (error) {
    dispatch(Creators.updateAppInfo(null));
  }
};

