/* 
userid int64
accesstoken string
reqdate int64
appversion string 
deviceid string
*/

const api = axios.create({
  // baseURL: BaseUrl[Environment.QC_SANDBOX],
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json;charset=utf-8'
  }
});

api.interceptors.response.use(
  (response) => {
    console.log('response: ', response);
    if (response.data && response.data.returncode === ErrorCode.SUCCESS) {
      return response.data.data;
    } else {
      console.log('response: ', response.data.returnmessage);
      return Promise.reject(new Error(response.data.returnmessage));
    }
  },
  (error) => {
    console.log('error: ', error.response);
    if (error.response) {
      return Promise.reject(new Error(error.response.data.message));
    } else if (error.request) {
      return Promise.reject(new Error('No response was received'));
    } else {
      return Promise.reject(new Error(error.message));
    }
  }
);

export function setAuthorizationHeader(userID, accessToken) {
  api.defaults.headers.userID = userID;
  api.defaults.headers.accessToken = accessToken;
  // api.defaults.headers.apiKey = API_KEY;
}

export default api;
